
import XHeader from 'vux/src/components/x-header/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Selector from 'vux/src/components/selector/index.vue'

import AttendanceList from '@/components/attendance/AttendanceList'
export default {
name: 'TeacherList',
components: {
XHeader,
Datetime,
Group,
Cell,
AttendanceList,
Flexbox,
FlexboxItem,
Selector
},
data() {
return {
selectTime: '',
showContent: [],
kindergarten: 0,
list: [
{value: 'xxx幼儿园', key: 0},
{value: 'xxx幼儿园', key: 1},
{value: 'xxx幼儿园', key: 2}
],
titleArr: [
'姓名', '班级', '职位', '到校', '离校', ''
],
attendanceList: [
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'}
]
}
},
methods: {
handleDetail: function() {
this.$router.push('/principal/attendance/teacherMonthDetail')
}
},
computed: {
/**
* 获取职工身份对应表
* @return {Object} 身份表
*/
getIdentity: function() {
return this.$store.getters.getIdentity
},
/**
* 组装职工列表
* @return {Array} 组装好的职工信息表
*/
getStaffAttendanceList: function() {
let arr = [], list = this.$store.getters.getStaffAttendance.list, identity = this.getIdentity
for(let i in list) {
arr.push({
name: list[i].name,
className: list[i].type == 32?list[i].title:'无',
position: identity[list[i].type],
in_time: list[i].in_time?list[i].in_time:'****',
out_time: list[i].out_time?list[i].out_time:'****',
})
}
return  arr
}
},
created() {

this.$store.dispatch('actionStaffAttendance', {schoolId: 10, date: '2016-07-22'})
}
}



